import React, { useEffect } from 'react'

import PricePage   from './pricePage'
import { useDispatch, useSelector } from 'react-redux'; 
import { PLAN__DETAILS } from '../../../actions/planDetail';
import { togglePriceSection } from '../../../actions/show_modal_Action';
import { getUserInfo_afterChanges } from '../../../actions/loginActions';

export default function Index() {

    const {showPrice, priceP_id} = useSelector(state =>  state.modalReducer)
    const authorized = useSelector(state =>  state.authorized)
    const priceDetail = useSelector(state =>  state.planPriceDetail.planPrice) 
  const dispatch = useDispatch()

  useEffect(() => {
   // dispatch(PLAN__DETAILS())
   }, [])

    return (<>
        { showPrice && priceP_id == 2 ? <PricePage clickToClosePrice={() => dispatch(togglePriceSection(false))} priceDetail={priceDetail } authorized={authorized} getUserInfo_afterChanges={() => dispatch(getUserInfo_afterChanges())}  dispatch={dispatch} />:''}
   </>)
}
